.header {
  margin-top: 40px;

  &__name {
    font-size: 5rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin: 0 0 0 15px;
  }
}

.nav {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  margin-top: 10px;
  padding: 0;

  &__link {
    &:link,
    &:visited {
      display: inline-block;
      font-size: $default-font-size;
      font-weight: 300;
      color: $color-white;
      text-decoration: none;
      text-transform: uppercase;
      margin-left: 25px;
    }
  }
}
