.form {
  padding: 20px;

  &__group:not(:last-child) {
    margin-bottom: 2rem;
  }

  &__input {
    font-weight: bold;
    background-color: #d9d8da;
    width: 70%;
    padding: 1rem 2rem;
    border-radius: 50px;
    border: none;
  }

  &__textarea {
    padding: 1rem 2rem;
    font-weight: bold;
    background-color: #d9d8da;
    width: 70%;
    border-radius: 10px;
    border: none;
  }
}
